import React from 'react'
import { conCotizacion, conFactura, conNota } from '../Models/Factura'
import Cliente, { ClienteNull, conCliente } from '../Models/Cliente'
import { ProductosContext, ProductosType } from './Producto'
import { updateOrPush, Estado, EstadoNull, GeneralFila } from '../Models/General'
import { formatoHora } from '../Utiles/Formatos'
import { Conexion } from '../Config/Coneccion'

interface State {
    estCliente: Estado
    estCotizacion: Estado
    estNota: Estado
    estFactura: Estado
    Facturas: GeneralFila[]
    Cotizaciones: GeneralFila[]
    Notas: GeneralFila[]
    Clientes: Cliente[]
}
export interface VentasType extends ProductosType, State {
    actualizar: () => void
    actCliente: () => void
    actCotizacion: () => void
    actNota: () => void
    actFactura: () => void
    findCliente: (c: string) => Cliente | undefined
    resetVenta: () => void
}
export const VentasContext = React.createContext<VentasType | null>(null)
class VentasProvider extends React.Component<any, State> {
    Facturas = 0;
    Cotizaciones = 0;
    Notas = 0;
    Clientes = 0;
    constructor(props: any) {
        super(props)
        this.state = {
            Facturas: [],
            Cotizaciones: [],
            Notas: [],
            Clientes: [],
            estCliente: EstadoNull,
            estCotizacion: EstadoNull,
            estNota: EstadoNull,
            estFactura: EstadoNull
        }
    }
    reset() {
        this.setState({
            estCliente: EstadoNull,
            estCotizacion: EstadoNull,
            estNota: EstadoNull,
            estFactura: EstadoNull
        })
    }
    render() {
        let seccionContext: ProductosType = this.context;
        this.set();
        return <VentasContext.Provider value={{
            ...this.state, ...seccionContext,
            actualizar: this.set.bind(this),
            actCliente: this.actCliente.bind(this),
            actCotizacion: this.actCotizacion.bind(this),
            actNota: this.actNota.bind(this),
            actFactura: this.actFactura.bind(this),
            findCliente: this.findCliente.bind(this),
            resetVenta: this.reset.bind(this)
        }}>
            {this.props.children}
        </VentasContext.Provider>
    }
    componentDidMount() {
        this.init();
    }
    init() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.length > 0) {
            if (seccionContext.data.Permisos.some(p => p === 'FACTURA'))
                conFactura().cargarListaCache((Facturas) => { this.setState({ Facturas }) });
            if (seccionContext.data.Permisos.some(p => (p === 'NOTA' || p === 'FACTURA')))
                conNota().cargarListaCache((Notas) => { this.setState({ Notas }) });
            if (seccionContext.data.Permisos.some(p => (p === 'COTIZACION' || p === 'NOTA' || p === 'FACTURA')))
                conCotizacion().cargarListaCache((Cotizaciones) => { this.setState({ Cotizaciones }) });
            if (seccionContext.data.Permisos.some(p => (p === 'COTIZACION' || p === 'NOTA' || p === 'FACTURA' || p === 'CLIENTE')))
                conCliente().cargarListaCache((Clientes) => { this.setState({ Clientes }) });
            this.set();
        } else {
            setTimeout(() => { this.init(); }, 1000);
        }
    }
    set() {
        this.actCliente();
        this.actCotizacion();
        this.actNota();
        this.actFactura();
    }
    actCliente() {
        let { upState } = this.context as ProductosType;
        const { estCliente, Clientes } = this.state;
        this.Clientes = upState({
            label: 'Cliente',
            comparar: this.Clientes,
            estado: estCliente,
            conexion: conCliente(),
            list: Clientes,
            setD: (Clientes) => this.setState({ Clientes }),
            setE: estCliente => this.setState({ estCliente }),
            conver: this.setCambioClientes.bind(this)
        })
    }
    actCotizacion() {
        let { upState, data: { Permisos } } = this.context as ProductosType;
        const { estCotizacion, Cotizaciones } = this.state;
        if (Permisos.some(p => p === ('COTIZACION' || 'NOTA' || 'FACTURA')))
            this.Cotizaciones = upState({
                label: 'Cotizacion',
                comparar: this.Cotizaciones,
                estado: estCotizacion,
                conexion: conCotizacion(),
                list: Cotizaciones,
                setD: Cotizaciones => this.setState({ Cotizaciones }),
                setE: estCotizacion => this.setState({ estCotizacion }),
                conver: updateOrPush
            })
    }
    actNota() {
        let { upState, data: { Permisos } } = this.context as ProductosType;
        const { estNota, Notas } = this.state;
        if (Permisos.some(p => p === ('NOTA' || 'FACTURA')))
            this.Notas = upState({
                label: 'Nota',
                comparar: this.Notas,
                estado: estNota,
                conexion: conNota(),
                list: Notas,
                setD: Notas => this.setState({ Notas }),
                setE: estNota => this.setState({ estNota }),
                conver: updateOrPush
            })
    }
    actFactura() {
        let { upState, data: { Permisos } } = this.context as ProductosType;
        const { estFactura, Facturas } = this.state;
        if (Permisos.some(p => p === 'FACTURA'))
            this.Facturas = upState({
                label: 'Factura',
                comparar: this.Facturas,
                estado: estFactura,
                conexion: conFactura(),
                list: Facturas,
                setD: Facturas => this.setState({ Facturas }),
                setE: estFactura => this.setState({ estFactura }),
                conver: updateOrPush
            })
    }
    findCliente(c: string) {
        const cli = this.state.Clientes.find(cli => cli.codigo === c);
        if (cli !== undefined) return cli;
        if (this.state.estCliente.status !== 2) this.actCliente();
        return ClienteNull;
    }
    arrayToClientes(c: any[]): Cliente[] {
        return c.map(a => {
            return {
                codigo: a[0],
                nombre: a[1],
                telefono: a[2],
                correo: a[3],
                contacto: a[4],
                direccion: a[5],
                tipo_contribuyente: a[6],
                retencion: a[7],
                estatus: a[8]
            }
        })
    }
    setCambioClientes(data: Cliente[], c: Cliente[], con: Conexion) {
        const push = this.arrayToClientes(data).filter(((d: Cliente) => {
            const i = c.findIndex(c => c.codigo === d.codigo);
            if (i > -1) {
                c[i] = d;
                return false;
            }
            return true;
        }));
        const Clientes = [...c, ...push];
        con.saveLista(Clientes);
        return Clientes;
    }
}
VentasProvider.contextType = ProductosContext;
export default VentasProvider;
export const SeccionConsumer = VentasContext.Consumer;