import React from 'react'
import { Conexion } from '../Config/Coneccion'
import { conCompra, conOrden } from '../Models/Compra'
import { arrayToFilaGeneral, Estado, EstadoNull, GeneralFila, updateOrPush } from '../Models/General'
import Proveedor, { conProveedor, ProveedorNull } from '../Models/Proveedor'
import { formatoHora } from '../Utiles/Formatos'
import { ProductosContext, ProductosType } from './Producto'
interface State {
    estOrdenes: Estado
    estCompras: Estado
    estProveedores: Estado
    Ordenes: GeneralFila[]
    Compras: GeneralFila[]
    Proveedores: Proveedor[]
}
export interface ComprasType extends ProductosType, State {
    actualizar: () => void
    actCompra: () => void
    actOrden: () => void
    actProveedor: () => void
    findProveedor: (p: string) => Proveedor | undefined
    resetCompra: () => void
}
export const ComprasContext = React.createContext<ComprasType | null>(null)
class ComprasProvider extends React.Component<any, State> {
    Ordenes = 0;
    Compras = 0;
    Proveedores = 0;
    constructor(props: any) {
        super(props)
        this.state = {
            Ordenes: [],
            Compras: [],
            Proveedores: [],
            estOrdenes: EstadoNull,
            estCompras: EstadoNull,
            estProveedores: EstadoNull
        }
    }
    render() {
        let seccionContext: ProductosType = this.context;
        this.set();
        return <ComprasContext.Provider value={{
            ...this.state, ...seccionContext,
            actualizar: this.set.bind(this),
            actCompra: this.actCompra.bind(this),
            actOrden: this.actOrden.bind(this),
            actProveedor: this.actProveedor.bind(this),
            findProveedor: this.findProveedor.bind(this),
            resetCompra: this.reset.bind(this)
        }}>
            {this.props.children}
        </ComprasContext.Provider>
    }
    reset() {
        this.setState({
            estOrdenes: EstadoNull,
            estCompras: EstadoNull,
            estProveedores: EstadoNull
        })
    }
    componentDidMount() {
        this.init();
    }
    init() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.length > 0) {
            if (seccionContext.data.Permisos.some(p => (p === 'COMPRA')))
                conCompra().cargarListaCache((Compras) => this.setState({ Compras }));
            conOrden().cargarListaCache((Ordenes) => { this.setState({ Ordenes }) });
            conProveedor().cargarListaCache((Proveedores) => { this.setState({ Proveedores }) });
        } else {
            setTimeout(() => this.init(), 1000);
        }
    }
    set() {
        this.actCompra();
        this.actOrden();
        this.actProveedor();
    }
    actCompra() {
        let { upState, data: { Permisos } } = this.context as ProductosType;
        const { estCompras, Compras } = this.state;
        if (Permisos.some(p => p === 'COMPRA'))
            this.Compras = upState({
                label: 'Compra',
                comparar: this.Compras,
                estado: estCompras,
                conexion: conCompra(),
                list: Compras,
                setD: Compras => this.setState({ Compras }),
                setE: estCompras => this.setState({ estCompras }),
                conver: updateOrPush
            })
    }
    actOrden() {
        let { upState, data: { Permisos } } = this.context as ProductosType;
        const { estOrdenes, Ordenes } = this.state;
        if (Permisos.some(p => p === ('COMPRA' || 'ORDEN')))
            this.Proveedores = upState({
                label: 'Orden',
                comparar: this.Proveedores,
                estado: estOrdenes,
                conexion: conOrden(),
                list: Ordenes,
                setD: Ordenes => this.setState({ Ordenes }),
                setE: estOrdenes => this.setState({ estOrdenes }),
                conver: updateOrPush
            })
    }
    actProveedor() {
        let { upState, data: { Permisos } } = this.context as ProductosType;
        const { estProveedores, Proveedores } = this.state;
        if (Permisos.some(p => p === ('COMPRA' || 'ORDEN' || 'PROVEEDOR')))
            this.Ordenes = upState({
                label: 'Proveedor',
                comparar: this.Ordenes,
                estado: estProveedores,
                conexion: conProveedor(),
                list: Proveedores,
                setD: Proveedores => this.setState({ Proveedores }),
                setE: estProveedores => this.setState({ estProveedores }),
                conver: this.setCambioProveedores.bind(this)
            })
    }
    findProveedor(p: string) {
        const pro = this.state.Proveedores.find(Pro => Pro.codigo === p);
        if (pro !== undefined) return pro;
        if (this.state.estProveedores.status !== 2) this.actProveedor();
        return ProveedorNull;

    }
    arrayToProveedor(p: any[]): Proveedor[] {
        return p.map(a => {
            return {
                codigo: a[0],
                nombre: a[1],
                telefono: a[2],
                correo: a[3],
                contacto: a[4],
                direccion: a[5],
                estatus: a[6]
            }
        })
    }
    setCambioProveedores(data: Proveedor[], p: Proveedor[], c: Conexion) {
        const push = this.arrayToProveedor(data).filter((d => {
            const i = p.findIndex(p => p.codigo === d.codigo);
            if (i > -1) {
                p[i] = d;
                return false;
            }
            return true;
        }));
        const Proveedores = [...p, ...push];
        c.saveLista(Proveedores);
        return Proveedores;
    }

}
ComprasProvider.contextType = ProductosContext;
export default ComprasProvider;
export const SeccionConsumer = ComprasContext.Consumer;