import React from 'react'
import { ConConfig } from '../Models/Config'
import { Estado, EstadoNull } from '../Models/General'
import Usuario, { conUsuario, userNull, UsuarioFila } from '../Models/Usuario'
import { formatoHora } from '../Utiles/Formatos';
import { setLineFun } from '../Config/OnLineCheker';
import { setEstadoUpdater } from '../Config/Updater';
import { Conexion, don } from '../Config/Coneccion'
interface Estados {
    Cliente: number,
    Compra: number,
    Config: number,
    Cotizacion: number,
    Departamento: number,
    Dolares: number,
    Factura: number,
    Nota: number,
    Orden: number,
    Plantilla: number,
    Producto: number,
    Proveedor: number,
    Tipo: number,
    Unidad: number,
    Usuario: number,
    Moneda: number,
    Ajuste: number,
}
interface State {
    cargando: boolean,
    fecha: string
    User: Usuario
    Permisos: Array<any>
    seccion: boolean
    usuarios: UsuarioFila[]
    estUsuarios: Estado,
    rif: string,
    online: boolean,
    estados: Estados
}
export type estadoLabel = 'Cliente' | 'Compra' | 'Config' | 'Cotizacion' | 'Departamento' | 'Dolares' | 'Factura' | 'Nota' | 'Orden' | 'Plantilla' | 'Producto' | 'Proveedor' | 'Tipo' | 'Unidad' | 'Usuario' | 'Moneda' | 'Ajuste'
interface PE {
    label: estadoLabel,
    estado: Estado,
    comparar: number,
    conexion: Conexion,
    list: any[],
    setE: (d: any) => void,
    setD: (d: any) => void,
    conver?: (d: any[], l: any[], c: Conexion) => any

}
export interface SeccionType {
    data: State,
    logout: () => void,
    usuarios: UsuarioFila[]
    user: () => void,
    atcUsuario: () => void
    upState: (p: PE) => number
}

export const SeccionContext = React.createContext<SeccionType | null>(null)
export class SeccionProvider extends React.Component<any, State> {
    interval: any;
    usuarios = 0;
    refEstado: any = null;
    rif = true;
    constructor(props: any) {
        super(props)
        this.state = {
            cargando: true,
            User: userNull,
            fecha: '',
            Permisos: [],
            seccion: true,
            usuarios: [],
            estUsuarios: EstadoNull,
            rif: '',
            online: true,
            estados: {
                Cliente: 1,
                Compra: 1,
                Config: 1,
                Cotizacion: 1,
                Departamento: 1,
                Dolares: 1,
                Factura: 1,
                Nota: 1,
                Orden: 1,
                Plantilla: 1,
                Producto: 1,
                Proveedor: 1,
                Tipo: 1,
                Unidad: 1,
                Usuario: 1,
                Moneda: 1,
                Ajuste: 1,
            }
        }
        setEstadoUpdater(this.upEst.bind(this));
        setLineFun(this.setOnline.bind(this));
    }
    setOnline(online: boolean) {
        this.setState({ online });
    }
    render() {
        this.atcUsuario();
        this.atcConfig();
        return <SeccionContext.Provider value={{
            data: this.state,
            logout: this.logout.bind(this),
            usuarios: this.state.usuarios,
            user: this.user.bind(this),
            atcUsuario: this.atcUsuario.bind(this),
            upState: this.upState.bind(this),
        }}>
            {this.props.children}
        </SeccionContext.Provider>
    }
    async logout() {
        try {
            //solicitar nuevos datos
            let token = localStorage.getItem('Authorization');
            let response = await fetch(don + '/api/Auth/close', { headers: { 'Authorization': 'Bearer ' + token } });
            localStorage.removeItem('Authorization');
            await response.json();
            //cargar nuevos datos
            this.user();
            //Actualizadar nuevos datos en cache
        } catch (err) {
            console.log(err);
        }
    }
    async user() {
        try {
            // //solicitar nuevos datos
            let token = localStorage.getItem('Authorization');
            let response = await fetch('/api/Auth/me', { headers: { 'Authorization': 'Bearer ' + token } });
            let data: Usuario = await response.json();
            // let data = { "id_usuario": 13, "usuario": "SOPORTE2", "nivel": 0, "fecha": "2021-11-05", "rol": "Administrador", "permisos": ["CLIENTE", "COMPRA", "CONFIG", "COTIZACION", "DEPARTAMENTO", "DOLARES", "FACTURA", "NOTA", "ORDEN", "PRODUCTO", "PROVEEDOR", "REGION", "TIPO", "UNIDAD", "USUARIO", "AJUSTE", "MONEDA", "REPORTE"] };
            //cargar nuevos datos
            this.setState({
                User: data,
                Permisos: data.permisos,
                seccion: (data.id_usuario !== 0),
                cargando: false
            })
            //Actualizadar nuevos datos en cache
        } catch (err) {
            console.log(err);
        }
    }
    atcUsuario() {
        const { estUsuarios, usuarios, seccion } = this.state;
        if (seccion)
            this.usuarios = this.upState({
                label: 'Usuario',
                comparar: this.usuarios,
                estado: estUsuarios,
                conexion: conUsuario(),
                list: usuarios,
                setD: usuarios => this.setState({ usuarios }),
                setE: estUsuarios => this.setState({ estUsuarios }),
            })
    }
    atcConfig() {
        if (!this.state.cargando && this.state.seccion) {
            if (this.state.rif === '' && this.rif) {
                this.rif = false;
                ConConfig('empresa').pull(({ numero_fiscal }: { numero_fiscal: string }) => this.setState({ rif: numero_fiscal }))
            }
        }
    }
    componentDidUpdate(p: any, s: State) {
        const { state: { seccion, cargando }, interval } = this;
        if ((interval === undefined) && seccion && !cargando)
            this.initUpdater();
    }
    initUpdater() {
        conUsuario().cargarListaCache(usuarios => this.setState({ usuarios }));
        this.interval = setInterval(() => this.updateEstado(), 1000)
    }
    componentDidMount() {
        this.user();
    }
    async setEmpresa() {
        try {
            //solicitar nuevos datos
            let token = localStorage.getItem('Authorization');
            let response = await fetch(don + '/api/Auth/getConfig', { headers: { 'Authorization': 'Bearer ' + token } });
            let { numero_fiscal } = await response.json();
            //cargar nuevos datos
            this.setState({ rif: numero_fiscal })
        } catch (error) {
            console.log(error);
        }
    }
    act = true;
    async updateEstado() {
        if (this.act)
            try {
                this.act = false;
                //solicitar nuevos datos
                let token = localStorage.getItem('Authorization');
                let response = await fetch(don + '/api/Auth/getEstado' + this.randon(), { headers: { 'Authorization': 'Bearer ' + token } });
                let estados = await response.json();
                //cargar nuevos datos
                this.saveEstado(await estados)
                this.act = true;
            } catch (error) {
                console.log(error);
            }
    }
    saveEstado(estados: Estados) {
        const estado = Object.values(this.state.estados).reduce((acc, val) => acc + val);
        const ne = Object.values(estados).reduce((acc, val) => acc + val);
        if (estado !== ne)
            this.setState({ estados });
        // const estado = localStorage.getItem('estado');
        // const newEstado = JSON.stringify(est);
        // if (estado !== newEstado)
        //     localStorage.setItem('estado', newEstado)
    }
    checkInternalEstato() {
        const estado = localStorage.getItem('estado');
        const actEstado = JSON.stringify(this.state.estados);
        if (estado !== null && estado !== actEstado)
            this.setState({ estados: JSON.parse(estado) });
    }
    async upEst(tabla: string) {
        try {
            //solicitar nuevos datos
            let token = localStorage.getItem('Authorization');
            let response = await fetch(don + '/api/Auth/estado/' + tabla + this.randon(), { headers: { 'Authorization': 'Bearer ' + token } });
            let est = await response.json();
            //cargar nuevos datos
            this.saveTableEstado(tabla, est);
        } catch (error) {
            console.log(error);
        }
    }
    saveTableEstado(tabla: string, est: number) {
        const estado = localStorage.getItem('estado');
        if (estado !== null) {
            let e = JSON.parse(estado);
            if (e[tabla] !== est) {
                e[tabla] = est;
                localStorage.setItem('estado', JSON.stringify(e));
            }
        } else {
            this.updateEstado();
        }
    }
    randon() {
        const rand = (Math.random() + '').substr(2);
        return '/' + rand;
    }
    upState({ label, estado, comparar, conexion, list, setE, setD, conver }: PE) {
        const { estados } = this.state;
        if (estados[label] === comparar) return comparar;
        // conexion.checkListaCache(list, setD, conver);
        if (estado.status !== 2) {
            setE({ status: 2, hora: '...ACTUALIZANDO' });
            this.setState({ estUsuarios: { status: 2, hora: '...ACTUALIZANDO' } })
            if (conver === undefined) conexion.cargarListaWeb(data => {
                setD(data);
                setE({ status: 3, hora: formatoHora(undefined) });
            });
            else conexion.cambios(data => {
                setD(conver(data, list, conexion));
                setE({ status: 3, hora: formatoHora(undefined) });
            });
        }
        return estados[label];
    }
    componentWillUnmount() { clearInterval(this.interval); }

}
export const SeccionConsumer = SeccionContext.Consumer;
