import React from 'react'
import { Conexion } from '../Config/Coneccion';
import { ConConfig } from '../Models/Config';
import { Estado, EstadoNull } from '../Models/General';
import { Departamento, Tipo, Producto, Unidad, conDepartamento, conProducto, conUnidad, conTipo, ProductoNull, ProductoVenta } from '../Models/Productos'
import { indexOfCallback } from '../Utiles/Filtros';
import { SeccionContext, SeccionType } from './Seccion'
interface State {
    estProducto: Estado
    estDepartamentos: Estado
    estTipos: Estado
    estUnidades: Estado
    Departamentos: Departamento[]
    Tipos: Tipo[]
    Productos: Producto[]
    Unidades: Unidad[]
    tasa: number,
    dolar: boolean
    impuesto: number
    precio1: number
    precio2: number
    precio3: number
}
export interface ProductosType extends SeccionType, State {
    actualizar: () => void
    actDepartamento: () => void
    actUnidad: () => void
    actTipo: () => void
    actProducto: () => void
    actConfig: () => void
    findProducto: (c: string) => Producto
    findProVen: (p: Producto, dolar: boolean) => ProductoVenta
    resetPro: () => void
}
export const ProductosContext = React.createContext<ProductosType | null>(null)
class ProductosProvider extends React.Component<any, State> {
    Departamentos = 0;
    Tipos = 0;
    Productos = 0;
    Unidades = 0;
    dolar = 0;
    constructor(props: any) {
        super(props)
        this.state = {
            Departamentos: [],
            Tipos: [],
            Productos: [],
            Unidades: [],
            estProducto: EstadoNull,
            estDepartamentos: EstadoNull,
            estTipos: EstadoNull,
            estUnidades: EstadoNull,
            tasa: 0,
            dolar: false,
            impuesto: 1,
            precio1: 1,
            precio2: 1,
            precio3: 1,
        }
    }
    render() {
        this.set();
        let seccionContext: SeccionType = this.context;
        return <ProductosContext.Provider value={{
            ...this.state, ...seccionContext,
            actualizar: this.set.bind(this),
            actDepartamento: this.actDepartamento.bind(this),
            actUnidad: this.actUnidad.bind(this),
            actTipo: this.actTipo.bind(this),
            actProducto: this.actProducto.bind(this),
            actConfig: this.configuraciones.bind(this),
            findProducto: this.findProducto.bind(this),
            findProVen: this.findProVen.bind(this),
            resetPro: this.reset.bind(this)
        }}>
            {this.props.children}
        </ProductosContext.Provider>
    }
    reset() {
        this.setState({
            estProducto: EstadoNull,
            estDepartamentos: EstadoNull,
            estTipos: EstadoNull,
            estUnidades: EstadoNull,
        })
    }
    componentDidMount() {
        this.init();
    }
    init() {
        let seccionContext: ProductosType = this.context;
        if (seccionContext.data.Permisos.length > 0) {
            conProducto().cargarListaCache((Productos) => { this.setState({ Productos }) });
            conDepartamento().cargarListaCache((data) => { this.setState({ Departamentos: data }) });
            if (seccionContext.data.Permisos.some(p => (p === 'UNIDAD' || p === 'PRODUCTO')))
                conUnidad().cargarListaCache((data) => { this.setState({ Unidades: data }) });
            if (seccionContext.data.Permisos.some(p => (p === 'TIPO' || p === 'PRODUCTO')))
                conTipo().cargarListaCache((data) => { this.setState({ Tipos: data }) });
        } else {
            setTimeout(() => this.init(), 1000);
        }
    }
    set() {
        this.actDepartamento();
        this.actUnidad();
        this.actTipo();
        this.actProducto();
        this.configuraciones();
    }
    actDepartamento() {
        let { upState } = this.context as ProductosType;
        const { estDepartamentos, Departamentos } = this.state;
        this.Departamentos = upState({
            label: 'Tipo',
            comparar: this.Departamentos,
            estado: estDepartamentos,
            conexion: conDepartamento(),
            list: Departamentos,
            setD: Departamentos => this.setState({ Departamentos }),
            setE: estDepartamentos => this.setState({ estDepartamentos })
        })
    }
    actUnidad() {
        let { upState, data: { Permisos } } = this.context as ProductosType;
        const { estUnidades, Unidades } = this.state;
        if (Permisos.some(p => p === ('UNIDAD' || 'PRODUCTO')))
            this.Unidades = upState({
                label: 'Tipo',
                comparar: this.Unidades,
                estado: estUnidades,
                conexion: conUnidad(),
                list: Unidades,
                setD: Unidades => this.setState({ Unidades }),
                setE: estUnidades => this.setState({ estUnidades })
            })
    }
    actTipo() {
        let { upState, data: { Permisos } } = this.context as ProductosType;
        const { estTipos, Tipos } = this.state;
        if (Permisos.some(p => p === ('TIPO' || 'PRODUCTO')))
            this.Tipos = upState({
                label: 'Tipo',
                comparar: this.Tipos,
                estado: estTipos,
                conexion: conTipo(),
                list: Tipos,
                setD: Tipos => this.setState({ Tipos }),
                setE: estTipos => this.setState({ estTipos })
            })
    }
    actProducto() {
        let { upState } = this.context as ProductosType;
        const { estProducto, Productos } = this.state;
        this.Productos = upState({
            label: 'Producto',
            comparar: this.Productos,
            estado: estProducto,
            conexion: conProducto(),
            list: Productos,
            setD: Productos => this.setState({ Productos }),
            setE: estProducto => this.setState({ estProducto }),
            conver: this.setCambioProductos.bind(this)
        })
    }
    configuraciones() {
        let seccionContext: SeccionType = this.context;
        if (this.dolar !== seccionContext.data.estados.Dolares) {
            this.dolar = seccionContext.data.estados.Dolares;
            ConConfig('Region').pull(data => this.setState({ impuesto: data.impuesto }));
            ConConfig('costo').pull(data => this.setState({ dolar: data.tasa }));
            ConConfig('utilidades').pull(data => this.setState(data));
            ConConfig('').forceGetWeb('/api/Dolares/valor', tasa => this.setState({ tasa }));
        }
    }
    findProducto(cod: string) {
        let pro = this.state.Productos.find(p => cod === p.codigo);
        if (pro !== undefined) {
            let costo2 = pro.costo;
            if (this.dolarValido()) if (pro.tasa !== 0) costo2 = (pro.costo * this.state.tasa / pro.tasa);
            if (pro.costo < 0.01) costo2 = 0.01;
            return { ...pro, costo: costo2, };
        }
        if (this.state.estProducto.status !== 2) this.actProducto();
        return ProductoNull;
    }
    findProVen(pr: Producto, dolares: boolean): ProductoVenta {
        let pro = this.state.Productos.find(p => pr.codigo === p.codigo);;
        if (pro === undefined) pro = pr;
        let { costo, precio1, precio2, precio3, tasa } = pro;
        if (dolares) {
            if (tasa !== 0) costo /= tasa;
            else if (this.state.tasa !== 0) costo /= this.state.tasa
        } else {
            if (tasa !== 0 && this.state.tasa !== 0) costo = parseFloat((costo / tasa).toFixed(2)) * this.state.tasa
        }
        return {
            ...pro, costo,
            monto1: costo * (1 + (precio1 / 100)),
            monto2: costo * (1 + (precio2 / 100)),
            monto3: costo * (1 + (precio3 / 100))
        }
    }
    arrayToProducto(p: any[]): Producto[] {
        return p.map(a => {
            return {
                codigo: a[0],
                departamento: a[1],
                descripcion: a[2],
                estatus: a[3],
                enser: a[4],
                tipo: a[5],
                unidad: a[6],
                medida: a[7],
                costo: a[8],
                precio1: a[9],
                precio2: a[10],
                precio3: a[11],
                cantidad: a[12],
                fecha_creacion: a[13],
                inventario: a[14],
                exento: a[15],
                tasa: a[16],
            }
        })
    }
    dolarValido() { return ((this.state.tasa !== 0) && this.state.dolar) }
    setCambioProductos(data: any[], p: Producto[], co: Conexion) {
        console.log(data);
        
        const push = this.arrayToProducto(data).filter((d => {
            const i = p.findIndex(p => p.codigo === d.codigo);
            if (i > -1) {
                p[i] = d;
                return false;
            }
            return true;
        }));
        const Productos = [...p, ...push];
        co.saveLista(Productos);
        return Productos;
    }
}
ProductosProvider.contextType = SeccionContext;
export default ProductosProvider;
export const SeccionConsumer = ProductosContext.Consumer;